import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { attemptLogin, checkSession } from '../../redux/asyncActions';

class SignIn extends Component {
    state = {};

    onEmailChange = event => {
        this.setState({
            email: event.target.value
        });
    };

    onPasswordChange = event => {
        this.setState({
            password: event.target.value
        });
    };

    onSubmitHandler = () => {
        const { email, password } = this.state;
        const payload = { email, password };

        this.props.logIn(payload);
    };
    render() {
        return (
            <article className="br3 shadow-5 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 center bg-white-70">
                <main className="pa4 black-80">
                    <fieldset
                        id="sign_up"
                        className="ba b--transparent ph0 mh0"
                    >
                        <legend className="f2 fw6 ph0 mh0">Sign In</legend>
                        <div className="mt3">
                            <label
                                className="db fw6 lh-copy f6"
                                htmlFor="email-address"
                            >
                                Email
                            </label>
                            <input
                                className="pa2 input-reset ba bg-transparent  hover-black w-100"
                                type="email"
                                name="email-address"
                                id="email-address"
                                onChange={this.onEmailChange}
                            />
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f6">
                                Password
                            </label>
                            <input
                                className="b pa2 input-reset ba bg-transparent  hover-black w-100"
                                type="password"
                                id="password"
                                onChange={this.onPasswordChange}
                            />
                        </div>
                    </fieldset>
                    <div className="">
                        <button
                            onClick={this.onSubmitHandler}
                            className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib br2"
                        >
                            Sign In
                        </button>
                        {!this.props.error || <div>{this.props.error}</div>}
                    </div>
                    <div className="lh-copy mt3">
                        <Link
                            to="/register"
                            className="f5 link dim black db pointer"
                        >
                            Register
                        </Link>
                    </div>
                </main>
            </article>
        );
    }
}
const mapDispatchToProps = dispatch => {
    return {
        logIn: userInfo => {
            dispatch(attemptLogin(userInfo));
        },
        checkSession: () => {
            dispatch(checkSession());
        }
    };
};

const mapStateToProps = state => {
    const { error } = state.reducer;
    return {
        error
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(SignIn);



